/* Styles for the login container */
.login-container {
    max-width: 600px;
    margin: 80px auto; /* Adjust the margin-top value according to your needs */
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Styles for the heading */
  .login-container h1 {
    text-align: center;
    color: #333;
  }
  
  /* Styles for the form */
  .login-container form {
    display: flex;
    flex-direction: column;
  }
  
  /* Styles for labels */
  .login-container label {
    margin-bottom: 8px;
    color: rgb(0, 0, 0);
  }
  
  /* Styles for input fields */
  .login-container input {
    padding: 8px;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 16px;
    color: #333; /* Set the text color for input fields */
  }
  
  /* Styles for the submit button */
  .login-container button {
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
  }
  
  /* Hover effect for the submit button */
  .login-container button:hover {
    background-color: #0056b3;
  }
  @media (max-width: 768px) {
    .login-container {
        max-width: 400px;
        margin: 40px auto; /* Adjust the margin-top value according to your needs */
        padding: 20px;
        border: 1px solid #ccc;
        border-radius: 5px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      }
  }
  