/* Navbar.css */

.navbar {
    background-color: #333;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo {
    font-size: 30px;
    color: #fff;
    font-weight: bold;
  }
  
  .nav-list {
    list-style-type: none;
    display: flex;
  }
  
  .nav-item {
    margin-right: 20px;
  }
  
  .nav-item a {
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    font-size: 18px;
    transition: color 0.3s ease-in-out;
  }
  
  .nav-item a:hover {
    color: #007BFF;
  }

  @media only screen and (max-width: 1000px){
    .nav-item a {
        text-decoration: none;
        color: #fff;
        font-weight: bold;
        font-size: 14px;
        transition: color 0.3s ease-in-out;
      }

      .logo {
        font-size: 20px;
        color: #fff;
        font-weight: bold;
      }
      .nav-item {
        margin-right: 10px;
      }
  }