/* Stilizare pentru body */
.contnt {
  margin: 0;
  padding: 0;
  background-color: #222;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media only screen and (min-width: 1000px) {
  h3 {
      margin-left: 25%;
      margin-right: 25%;
      margin-top: 5%;
  }
}
h3{
  font-size: 20px;
}
@media only screen and (max-width: 1000px) {
h3 {
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 15%;
}
.form-container {
  background-color: #444;
  box-shadow: 0 7px 12px #007BFF;
  border-radius: 8px;
  padding: 20px;
  width: 80%;
  max-width: 200px;
  text-align: center;
}
}

.links{
  text-decoration: none;
  color: #fff;
}

/* Stilizare pentru containerul de formular */
.form-container {
  background-color: #444;
  box-shadow: 0 7px 12px #007BFF;
  border-radius: 8px;
  padding: 20px;
  width: 100%;
  max-width: 400px;
  text-align: center;
}

/* Stilizare pentru etichete și câmpuri de intrare */
label {
  display: block;
  margin-top: 10px;
  font-weight: 600;
  margin-left: -280px;
}

input[type="text"],
input[type="email"],
textarea {
  width: 90%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #333;
  border-radius: 5px;
  font-size: 16px;
  background-color: #555;
  color: #fff;
}

/* Stilizare pentru butonul de trimitere */
input[type="submit"] {
  background-color: #007BFF;
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
  transition: background-color 0.3s ease-in-out;
}

input[type="submit"]:hover {
  background-color: #0056b3;
}

/* Stilizare pentru textul de eroare sau succes */
.message {
  text-align: center;
  font-weight: 600;
  margin-top: 10px;
  color: #fff;
}

.success {
  color: #28a745;
}

.error {
  color: #dc3545;
}

/* Stilizare pentru footer */
.footer {
  text-align: center;
  margin-top: 20px;
  padding: 10px;
}

.footer h4 {
  color: #ccc;
}

.links a {
  color: #fbfbfb;
  text-decoration: none;
  margin: 0 10px;
}
