/* Payment.css */

.payment-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    color: #fff;
    background-color: #222;
    height: 100vh; /* Full height of the viewport */
  }
  
  .payment-title {
    margin-bottom: 20px;
    color: #ffffff;
  }
  
  .payment-description {
    color: #53a6ff;
    max-width: 800px;
    text-align: center;
    margin-bottom: 30px;
  }
  
  .plan-options {
    display: flex;
    gap: 10px;
  }
  
  .plan-btn {
    background-color: #53a6ff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 600;
    transition: background-color 0.3s ease-in-out;
  }
  
  .plan-btn.active {
    background-color: #005bbc;
  }
  
  .payment-form {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    width: 100%;
    margin-top: 20px;
  }
  
  label {
    margin-top: 10px;
    font-weight: 600;
  }
  
  input {
    width: 100%;
    padding: 16px;
    margin-bottom: 15px;
    border: 1px solid #555;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .submit-btn {
    background-color: #007BFF;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    font-size: 18px;
    font-weight: 600;
    transition: background-color 0.3s ease-in-out;
  }
  
  .submit-btn:hover {
    background-color: #0056b3;
  }
  
  a {
    text-decoration: none;
    color: #fff;
    margin-top: 10px;
  }
  