/* JoinUs.css */

.join-us {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    color: #fff;
  }
  
  h1 {
    margin-bottom: 20px;
    color: #001f41;
  }
  
  .main-description {
    max-width: 600px;
    text-align: center;
    margin-bottom: 30px;
    color: #000e1d;
  }
  
  .subscription-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .card {
    background-color: #444;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    width: 100%;
    max-width: 300px;
  }
  
  .card h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .card p {
    font-size: 16px;
    margin-bottom: 15px;
  }
  
  .subscribe-btn {
    background-color: #007BFF;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 18px;
    font-weight: 600;
    transition: background-color 0.3s ease-in-out;
  }
  
  .subscribe-btn:hover {
    background-color: #0056b3;
  }
  
  .footer {
    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .footer a {
    color: #fff;
    text-decoration: none;
    margin: 0 10px;
  }
  
  /* Responsive styles */
  @media only screen and (max-width: 768px) {
    .subscription-cards {
      flex-direction: column;
      align-items: center;
      margin-right: 30px;
    }
  
    .card {
      max-width: 100%;
      margin-top: 20px;
    }
  
    .footer {
      margin-top: 10px;
    }
  }
  