
@media only screen and (max-width: 1000px) {
    .title{
        margin-top: 35%;
        font-size: 60px;
    }
    .description{
        font-size: 25px;
        margin-left: 10%;
        margin-right: 10%;
    }
    .btn{
        background-color: #0029FF;
        border: none;
        color: #ffffff;
        font-size: 15px;
        margin: 10px;
        border-radius: 15%;
        padding: 10px;
        opacity: 1;
    }
  }

@media only screen and (min-width: 1000px) {
    .title{
        margin-top: 10%;
        font-size: 80px;
        margin-left: 20px;
        margin-right: 20px;
    }
    .description{
        font-size: 30px;
        margin-left: 15%;
        margin-right: 15%;
    }
    .btn{
        background-color: #0029FF;
        border: none;
        color: #ffffff;
        font-size: 20px;
        margin: 10px;
        border-radius: 15%;
        padding: 10px;
        opacity: 1;
    }
    .footer {
        background-color: #313131;
        position: fixed;
        width: 100%;
        bottom: 0;
        color: white;
        font-size: 18px;
    }
  }

a{
    text-decoration: none;
    color: white;
}

body {
    margin: 0;
    padding: 0;
    height: 400px;
}


body{
    background-color: #ffffff;
}

.btn:hover{
    box-shadow: 0 6px 8px 0 rgba(132, 132, 132, 0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.link{
    color: #0029FF;
}

.footer {
    text-align: center;
    background-color: #333;
    color: #fff;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;
  }
  
  .footer div {
    margin-bottom: 15px; /* Adjust margin as needed */
  }
  
  .footer h4 {
    font-size: 16px;
    margin: 0;
  }
  
  .links {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  
  .links a {
    color: #fff;
    text-decoration: none;
    margin: 0 20px; /* Adjust margin as needed */
    font-size: 14px;
  }
  .links a:hover {
    color: #007BFF;
  }  