/* Videos.css */
  
  .videos-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .video-card {
    width: 300px;
    margin: 20px;
    padding: 20px;
    border: 1px solid rgb(152, 152, 152);
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .video-thumbnail {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 6px;
  }
  
  .video-description {
    margin-top: 10px;
    font-size: 16px;
  }
  
  .watch-now-btn {
    display: block;
    width: 100%;
    padding: 12px;
    margin-top: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out;
  }
  
  .watch-now-btn:hover {
    background-color: #0056b3;
  }
  
  .video-stats {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
  }
  
  .views,
  .likes {
    flex: 1;
    text-align: left;
  }
  
  .likes {
    text-align: right;
  }
  
  /* Media Query for smaller screens (e.g., phones) */
  @media (max-width: 768px) {
    .video-card {
      width: 100%;
    }
  
    .video-thumbnail {
      height: 150px;
    }
  }
  